import React, { useEffect, useState } from 'react';
import DateTimePicker from 'react-datetime-picker';
import './App.css';
import { fetchCars, addCar, removeCar } from './FetchCars';
import ConfirmBox from './ConfirmBox';
import findImg from './findIcon.jpg';

export default function Lots() {

    const [car, setCar] = useState('');
    const [plate, setPlate] = useState('');
    const [date, setDate] = useState(new Date());
    const [dateOut, setDateOut] = useState(new Date());

    const [cars, setCars] = useState([]);

    const [confirmOpen, setConfirmOpen] = useState(false)

    const [carOutInfo, setcarOutInfo] = useState([{
        _id: '',
        car: '',
        plate: '',
        date: '',
        dateOut: ''
    }])

    const [findPush, setFindPush] = useState([]);
    const [findInput, setFindInput] = useState('');

    useEffect(() => {
        fetchCars(setCars)
        
    }, []);

    setInterval(() => {
        setDateOut(new Date())
    }, 1000)

    const onChangeEventCar = (e) => {
        e.preventDefault();
        setCar(e.target.value.replace(/[^\w\s]/gi, ""))
    }

    const onChangeEventPlate = (e) => {
        e.preventDefault();
        setPlate(e.target.value.replace(/[^\w\s]/gi, ""))
    }

    const handleConfirmBox = () => {

        if (!confirmOpen) {
            document.querySelector('.confirmBackground').style.display = "flex"
            document.querySelector('.confirmContainer').style.display = "flex"
            setConfirmOpen(true)
        }

        else {
            document.querySelector('.confirmBackground').style.display = "none"
            document.querySelector('.confirmContainer').style.display = "none"
            setConfirmOpen(false)
        }
    } 

    const passData = (_id, car, plate, date, dateOut) => {
    console.log(dateOut)
    
    let newArr = [];
    newArr.push({_id, car, plate, date, dateOut});
    setcarOutInfo(newArr);
    }

    const check = () => {
    let _id = carOutInfo[0]._id;
    let car = carOutInfo[0].car;
    let plate = carOutInfo[0].plate;
    let date = carOutInfo[0].date;
    let dateOut = carOutInfo[0].dateOut;

    removeCar(_id, car, plate, date, dateOut, setCars, setCar, setPlate, handleConfirmBox, setFindPush)
    }

    const onChangeEventFindByOlate = (e) => {

    e.preventDefault();
        setFindInput(e.target.value);
    }

    const findByPlate = () => {
    
    let arr = [];
    cars.forEach(item => {
        if(item.plate.includes(findInput)) {
            arr.push(item);
setFindPush(arr);
setFindInput('');
        }
    })
    }

    return(
        <div>
            <div className='container'>
            <input
                type='text'
                placeholder='find by plate'
                value={findInput}
                onChange={onChangeEventFindByOlate}
                />
                <button disabled={!findInput} onClick={() => findByPlate()}><img src={findImg} width="16px"/></button>
            </div>
            <div className='container'>
                <div>
                {findPush.map(item => {
                    const {_id, car, plate, date} = item

                    return(
                        <div className='container' key={_id}>
                        <div className='items' key={_id}>
                            <div className='item'>
                                <span className='spanDecoration'>car: </span> {car}
                                <span className='spanDecoration'>plate: </span> {plate}
                            </div>
                            <div className="item">
                                <span className='spanDecoration'>Time In:</span>   {date}
                            </div>
                            <div className='item'>
                                <span className='spanDecoration'>Time out:</span>   {dateOut.toString()}
                                {/* <DateTimePicker
                                className="out"
                                value={dateOut}
                                onChange={value => setDateOut(value)}
                                dateFormat="MM/dd/yyyy h:mm aa"
                                /> */}
                            </div>
                        </div>
                        <div className='items'>
                            <button onClick={() => {handleConfirmBox(); passData(_id, car, plate, date, dateOut)}}>Car out</button>
                        </div>
                    </div>
                    )
                })}
        </div>
            </div>
            <div className='container'>
                <h2>LOTS AVAILABLE - {25 - cars.length}</h2>
            </div>
            <div className='container'>
                <input
                type='text'
                placeholder='car make'
                value={car}
                onChange={onChangeEventCar}
                />
                <input
                type='text'
                placeholder='licence plate'
                value={plate}
                onChange={onChangeEventPlate}
                />
                <div className="pickerDisable">
                {/* <DateTimePicker 
                className="picker"
                value={date}
                onChange={value => setDate({value})}
                dateFormat="MM/dd/yyyy h:mm aa"/> */}
                </div>
            </div>

            <div className='container'>
                <button disabled={!car || !plate || cars.length >= 25}
                onClick={() => addCar(car, plate, date, setCar, setPlate, setCars)}>{cars.length >= 25 ? "Lots are full" : "Add car"}</button>
            </div>
            <div className='container'>
                <div>
                {cars.map(element => {
                    const {_id, car, plate, date} = element;
                    return(
                        <div className='container' key={_id}>
                            <div className='items' key={_id}>
                                <div className='item'>
                                    <span className='spanDecoration'>car: </span> {car}
                                    <span className='spanDecoration'>plate: </span> {plate}
                                </div>
                                <div className="item">
                                    <span className='spanDecoration'>Time In:</span>   {date}
                                </div>
                                <div className='item'>
                                    <span className='spanDecoration'>Time out:</span>   {dateOut.toString()}
                                    {/* <DateTimePicker
                                    className="out"
                                    value={dateOut}
                                    onChange={value => setDateOut(value)}
                                    dateFormat="MM/dd/yyyy h:mm aa"
                                    /> */}
                                </div>
                            </div>
                            <div className='items'>
                                <button onClick={() => {handleConfirmBox(); passData(_id, car, plate, date, dateOut)}}>Car out</button>
                            </div>
                        </div>
                    )
                })}
                </div>

                <ConfirmBox
            title={"Are you double sure?"}
            description={carOutInfo.map(element => {
            const {_id, car, plate, date, dateOut} = element;
            let durationTime = ((new Date(dateOut) - new Date(date))/3600000)
            let duration = Math.ceil(durationTime.toFixed(1))
                return(
                    <div key={_id} className="items" >
                        <div className="item">
                        <span className="spanDecoration">CAR:</span> &nbsp; {car} &nbsp; <span className="spanDecoration">PLATE</span> &nbsp; {plate}
                        </div>
                        <div className="item"><span className="spanDecoration">TIME IN:</span> &nbsp;{date}</div>
                        <div className="item"><span className="spanDecoration">TIME OUT:</span> &nbsp;{dateOut.toString()}</div>
                        <div className="item"><span className="spanDecoration">DURATION:</span> &nbsp;{duration} hours</div>
                    </div>
                )
            })}
            handleCancel={() => handleConfirmBox()}
            handleConfirm={() => check()}
            />
            </div>
        </div>
    )
}