import { useEffect, useState } from "react";
import { deleteAll, fetchCarsHistory } from "./FetchCars";
import ConfirmBox from "./ConfirmBox";

function History() {

    const [carsHistory, setCarsHistory] = useState([]);
    const [confirmOpen, setConfirmOpen] = useState(false)
    
    useEffect(() => {
        fetchCarsHistory(setCarsHistory)
    }, [])

    const handleConfirmBox = () => {
        if (!confirmOpen) {
            document.querySelector('.confirmBackground').style.display = "flex"
            document.querySelector('.confirmContainer').style.display = "flex"
            setConfirmOpen(true)
            console.log(confirmOpen)
        }
        else {
            document.querySelector('.confirmBackground').style.display = "none"
            document.querySelector('.confirmContainer').style.display = "none"
            setConfirmOpen(false)
            console.log(confirmOpen)
        }
    } 

    return(
        <div>
            <div className="container">
            <button disabled={carsHistory.length === 0} onClick={() => handleConfirmBox()}>CLEAR ALL</button>
            </div>
            <div className="container">
            <ConfirmBox
            title={"Are you sure?"}
            handleCancel={() => handleConfirmBox()}
            handleConfirm={() => deleteAll(setCarsHistory, handleConfirmBox)}
            />
            </div>
            {carsHistory.map(item => {
                const { _id, car, plate, date, dateOut } = item;
                let duration = ((new Date(dateOut) - new Date(date))/3600000)
                return(
                    <div className="container" key={_id}>
                    <div className="items" >
                        <div className="item">
                        <span className="spanDecoration">CAR:</span> &nbsp; {car} &nbsp; <span className="spanDecoration">PLATE</span> &nbsp; {plate}
                        </div>
                        <div className="item"><span className="spanDecoration">TIME IN:</span> &nbsp;{date}</div>
                        <div className="item"><span className="spanDecoration">TIME OUT:</span> &nbsp;{dateOut}</div>
                        <div className="item"><span className="spanDecoration">DURATION:</span> &nbsp;{duration} hours</div>
                    </div>
                    </div>
                )
            })}
        </div>
    )
}

export default History;