import axios from "axios";

const fetchCars = (setCars) => {
    axios.get('http://localhost:4000')
    .then(({data}) => {
        setCars(data);
        console.log(data);
    })
}

const fetchCarsHistory = (setCarsHistory) => {
    axios.get('http://localhost:4000/history')
    .then(({data}) => {
        setCarsHistory(data);
    })
}

const addCar = (car, plate, date, setCar, setPlate, setCars) => {
    axios.post('http://localhost:4000/saveCar', { car, plate, date })
    .then(() => {
        setCar('');
        setPlate('');
        fetchCars(setCars);
    })
}

const removeCar = (_id, car, plate, date, dateOut, setCars, setCar, setPlate, handleConfirmBox, setFindPush) => {
    axios.post('http://localhost:4000/deleteCar', {_id, car, plate, date, dateOut})
    .then(() => {
        console.log(_id);
        fetchCars(setCars);
        setCar('');
        setPlate('');
        handleConfirmBox();
        setFindPush([]);
    });
}

const deleteAll = (setCarsHistory, handleConfirmBox) => {
    axios.post('http://localhost:4000/deleteMany', {})
    .then(() => {
        fetchCarsHistory(setCarsHistory);
        handleConfirmBox();
    })
}

export { fetchCars, addCar, removeCar, fetchCarsHistory, deleteAll };